import JumpBubble from "./JumpBubble";


import { configData as config } from "./config";
import { Base } from "./base";
import { Audio, Video } from "./model";
import { Common, Sys_msg, ModelPop, ProductPop } from "./message";



const baseClass = new Base(), common = new Common(), audio, video;
var V = function(selector) {
	let baseClassa = new Base(selector);
    return baseClassa;
}



function bubbleFun(){
	let list1 = [
		'/img/bubble/blue.png',
		'/img/bubble/green.png',
		'/img/bubble/orange.png',
		'/img/bubble/pink.png', //注：路径是相对html的路径，因为该路径最终会放到img标签的src上
	];

	const bubble = new JumpBubble(document.getElementById("paopaoCanvas0"),{
		effect: 'linear',
		speed: 'slow',
		width: 27,
		isToAlapha: true,
		diffWidth: 20
		//isToAlapha: i === 0 ? false : true, //演示，第二个设置不进行透明度变化
	});
	let s = 0;
	setInterval(() => {
		if(s > list1.length - 1){
			s = 0;
		}
		let img = new Image();
			img.src = list1[s];
		    img.onload = function(){
		    	bubble.create(list1[s]);
				s++
		    }
	},320);

	var heartNum = 0.1,setHeartNumTime;
	setHeartNumTime = setInterval(function(){
        heartNum += 0.1;
        document.querySelector('.heart em').innerHTML = heartNum.toFixed(1);
    },365);

}



/* 
 * DOMContentLoaded
 */
window.addEventListener("load",function(){
	baseClass.loadingFnDoing(config['loading'], function(){
        let loadingEle = document.querySelector('.loading');
        loadingEle.className += ' hidden';

		audio = new Audio();
		video = new Video();

		common.init();   

        const showTime = video.init(config['video']);

		document.querySelector('.poster').onclick = function(){
			showTime.play();
			this.style.visibility = 'hidden';
		}

		// 背景音乐
		//const bgMusic = audio.init(config['audio']['bg']);
		

		const chatRoomNode = document.getElementById("chatList");
		const sysMsg = new Sys_msg(chatRoomNode);
		const choseVolume = document.querySelectorAll('.choseVolume');

		let starVolume, endVolume, volumeName;
		// 开始音乐
		
		for(let i = 0; i < choseVolume.length; i++){
		  	  choseVolume[i].addEventListener("click", function(e){
		  	  		 
			  	    volumeName = this.getAttribute('data-name');

			  	    starVolume = audio.init(config['audio'][volumeName]['start']);
					endVolume = audio.init(config['audio'][volumeName]['end']);

					showTime.play();  // 视频播放
			  	    //bgMusic.play();
			  	//     if(!volumeId){
			  	//     	// audio.setVolume(bgMusic, 1.0);
			  	//     }else{
						// starVolume.play();  // 背景音乐播放
			  	//     }
			  	    
			  	    baseClass.sectionChange("showTime");
			  	    // sysMsg.init();    // 默认聊天输出

			  	    bubbleFun();      // 泡泡方法



			  	    audio.eventTester(starVolume, 'play', function(){
						video.setVolume(showTime, 0.6);
					})

					audio.eventTester(starVolume, 'ended', function(){
						video.setVolume(showTime, 1);
					})

					audio.eventTester(endVolume, 'play', function(){
						video.setVolume(showTime, 0.6);
					})

					audio.eventTester(endVolume, 'ended', function(){
						video.setVolume(showTime, 1);
					})



			  	    e.preventDefault();
			  })
		}

		let iconVolume = document.querySelector('.icon-volume');
		iconVolume.addEventListener("click", function(){
			if(!starVolume) return false;
			if(V(this).hasClass('off')){
				this.className = 'icon-volume';
				// audio.setVolume(bgMusic, 0.3);
				audio.setMuted(starVolume, false);
				audio.setMuted(endVolume, false);
				video.setVolume(showTime, 0.6);
			}else{
				this.className += ' off';
				// audio.setVolume(bgMusic, 1);
				audio.setMuted(starVolume, true);
				audio.setMuted(endVolume, true);
				video.setVolume(showTime, 1);
			}	
			
		})



		const ruleScrollEle = document.querySelector('.p-model-con');
		baseClass.overscroll(ruleScrollEle);
		
		const chatRoom = document.querySelector('.chat-room');
		baseClass.overscroll(chatRoom);

		const msgInput = document.querySelector('.msg-input'),
			  msglist = document.querySelector('.msglist'),
			  msglistli = msglist.querySelectorAll('li'),
			  msgText = msgInput.querySelector('input');
		msgInput.onclick = function(){
			if(V('.msglist').hasClass('hidden')){
				msglist.className = 'msglist';
			}else{
				msglist.className = 'msglist hidden';
			}
		}


		var curIndex = 0;
		for(let i = 0; i < msglistli.length;  i++){
            let msglistliNode = msglistli[i];
            msglistliNode.addEventListener('click', function(e){
				msglistli[curIndex].className = "";
				curIndex = i;
				msglistliNode.className = msglistliNode.className ? '' : 'hover';
				msgText.value = this.querySelector('span').innerHTML;
			})       
        }


		const sendBtn = document.querySelector('.msg-btn');
		sendBtn.addEventListener('click', function(e){
			let sendMsg = msgText.value;
			if(!sendMsg || sendMsg == '点击评论') return false;
			sysMsg.append(sendMsg, true);
			msgText.value = "";
		})




		const modelPop = new ModelPop();
		modelPop.init();


		var productPopSwiper = new Swiper('#productPop', {
	        nextButton: '.swiper-button-next',
	        prevButton: '.swiper-button-prev',
	        effect: 'cube'
	    });

	    const productPop = new ProductPop();
	    	  productPop.init();


		// video.setVolume(ad, 0);
		let checkTime = [];
		video.eventTester(showTime, 'timeupdate', function(){
			let time = parseInt(showTime.currentTime),
				duration = parseInt(showTime.duration);

			let timeline = config['model']['timeline'],
				xwzChatLine = config['chat'][volumeName];
			// console.log(volumeName, time, duration);
			// console.log(volumeName, time, duration, timeline);

			
			if(checkTime.indexOf(time) < 0){
				checkTime.push(time);

				for(let i = 0; i < timeline.length; i++){
					if(time == timeline[i]['time']){
						modelPop.show(timeline[i]['name']);
					}
				}

				for(let k = 0; k < xwzChatLine.length; k++){
					if(time == xwzChatLine[k]['time']){
						sysMsg.append(xwzChatLine[k]['text'], false, xwzChatLine[k]['avaName'], showTime.currentTime);
					}
				}

				if(volumeName == 'xwz'){
					switch(time){
						case 27:
						  productPop.show(function(){
					    		productPopSwiper.update();
					      });
						  break;
						case 85:
						  productPop.show(function(){
					    		productPopSwiper.update();
					      });
						  break;
						case 190:
						  productPop.show(function(){
					    		productPopSwiper.update();
					      });
						  break;
						default:
						  
					}
				}else{
					switch(time){
						case 28:
						  productPop.show(function(){
					    		productPopSwiper.update();
					      });
						  break;
						case 85:
						  productPop.show(function(){
					    		productPopSwiper.update();
					      });
						  break;
						case 175:
						  productPop.show(function(){
					    		productPopSwiper.update();
					      });
						  break;
						default:
						  
					}
				}
				switch(time){
					case 1:
					  starVolume.play();
					  break;
					default:
					  
				}
					
				
			} 
		});



	})
})












